export class State {
  #value;
  #subscribers = [];

  constructor(value) {
    this.#value = value;
  }

  get() {
    return this.#value;
  }

  set(newValue) {
    if (this.#value === newValue) return;
    this.#value = newValue;
    this.notify();
  }

  subscribe(handler) {
    if (this.#subscribers.includes(handler)) {
      console.warn('Already subscribed handler.'); //    or 

      return;
    }
    this.#subscribers.push(handler);
  }

  notify() {
    this.#subscribers.forEach(subscriber => subscriber(this.#value));
  }
}
